*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.app{
  background-image: url('./assests/images/back_1.png');
  background-repeat: no-repeat;
  background-position: bottom;
  transition: 0.4 ease-out;
  max-width: 600px;
  margin: 0 auto;
  box-shadow: 0 2px 29.12px 2.88px rgba(0,0,0,.14);
}
body {
  background: #2c3e50;
}
.app.dark{
  background-image: url('./assests/images/back_2.png');
}
.app.sunny{
  background-image: url('./assests/images/sunny.jpg');
}
.app.windy{
  background-image: url('./assests/images/windy.jpg');
}
.app.cloud{
  background-image: url('./assests/images/cloud.jpg');
}
.app.rain{
  background-image: url('./assests/images/rain.jpg');
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, #0003 , #00000063);    box-shadow: 0 2px 29.12px 2.88px rgba(0,0,0,.14);
}
.search{ 
  width: 100%;
  margin: 0px 0px 75px;
  height: 40px;
  border: 1px solid #e0dfdf;
  padding: 6px;
  font-size: 19px;
  border-radius: 0px 0px 15px 15px;
  box-shadow: 0 0 2px 1px #00000036;
  font-weight: bold;
  text-transform: capitalize;
}
.searchbox {
  text-align: center;
  width: 90%;
  margin: 0 auto;
  position: relative;
}
.locationbox {
  width: 90%;
  margin: 0 auto;
}
.locationname {
  font-size: 38px;
  color: #fff;
  text-align: center;
  font-weight: bold;
  margin: 2px;
}
.datetime {
  font-size: 15px;
  color: #fff;
  text-align: center;
  font-weight: bold;
  font-style: italic;
  margin: 5px;
}.temperature span {
  background: #ffffff8f;
  padding: 30px 100px 30px 100px;
  display: inline-block;
  border-radius: 15px;
  margin: 20px;
  box-shadow:0 0 6px 0px #000000b3;
  color: #fff;
  font-size: 45px;
  font-weight: bold;
  text-shadow: 0 0 2px black;
}
.temperature {
  text-align: center;
}
.weather {
  color: #fff;
  font-size: 30px;
  font-weight: bold;
  text-transform: capitalize;
  text-align: center;
}
.temperature h2{
  font-size: 20px;
  clear: both;
  text-align: center;
}

  .searchinitial {
    text-align: center;
    font-size: 22px;
    color: #fff;
    font-weight: bold;
}
.temperature span img {
  float: left;
  margin-top: -30px;
}
.autosuggesion {
  position: absolute;
  top: 40px;
  width: 100%;
  text-align: left;
  z-index: 9999;
  padding: 0px 10px;
}
.autosuggesion li {
  background: #fff;
  width: 100%;
  padding: 10px;
  list-style-type: decimal;
  cursor: pointer;
  font-weight: bold;
  color: #3F51B5;
  font-size: 16px;
}

.weatherother li {
  width: 50%;
  float: left;
  padding: 5px;
  list-style-type: none;
  font-size: 17px;
  color: #fff;
  text-align: center;
  margin: 5px 0px 0px 0px;
}
.weatherother {
  border-top: 1px solid #ccc;
  margin-top: 15px;
}